import styled from 'styled-components';

export const Avatar = styled.img`
  width: 300px;
  height: 300px;
  @media print {
    margin-right: 20px;
  }
  @media (max-width: 800px) {
    margin-right: 20px;
  }
  @media (max-width: 350px) {
    margin-right: 0px;
  }
`;



